<template>
  <div id="Start">
    <div class="barcontainer">
    	<div class="bar"></div>
    	<div class="barCont"><img src="@/assets/money.png" alt="Coin" class="coin" style></div>
	</div>
	<div class="task">
		<div id="wrapHelp">
      <div id="taskbar">
        <p>   ,         .       XV-XVII     ,  <div class="textWrap" id="firstWrap"></div>.        <div class="textWrap" id="secondWrap"></div>  <div class="textWrap" id="thirdWrap"></div>. <br>
                   <div class="textWrap" id="fourthWrap"></div>  .            ,    ,       .    , ,       .  ,   ,        <div class="textWrap" id="fifthWrap"></div>,  , <div class="textWrap" id="sixthWrap"></div>,          . <br>
             ?   ,        ,      ,        .  ,   <div class="textWrap" id="seventhWrap"></div>,            <div class="textWrap" id="eigthWrap"></div>,            .    .     .      ,  <div class="textWrap" id="ninethWrap"></div>,    ,      ,  <div class="textWrap" id="tenthWrap"></div>   , ,                   .<br>
              ,    ,       <div class="textWrap" id="elevenWrap"></div>  !
        </p>
      </div>
		</div>
		<div class="text">
      <div id="readybar">
        <span> </span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
        <span> </span>
        <span></span>
        <span></span>
        <span></span>
        <span> </span>
        <span></span>
      </div>
		</div>
	</div>
	<div id="next" @click="next">&#10230;</div>
    <div id="home" @click="home">&#10229;</div>
  </div>
</template>

<script>
export default{
  name: 'Fourth',
  props: ['progress'],
  data(){
  	return{
      user: []
  	}
  },
  methods:{
  	next: function(){
  		this.$emit('update', {
			 progress: 45
		  });
      this.$router.push('FinalStory')
    },
    home: function(){
    	this.$emit('update', {
			 progress: 5
		  });
      this.$router.push('Main')
    },
    },
  mounted(){
    dragula([
      document.querySelector("#firstWrap"),
      document.querySelector("#secondWrap"),
      document.querySelector("#thirdWrap"),
      document.querySelector("#fourthWrap"),
      document.querySelector("#fifthWrap"),
      document.querySelector("#sixthWrap"),
      document.querySelector("#seventhWrap"),
      document.querySelector("#eigthWrap"),
      document.querySelector("#ninethWrap"),
      document.querySelector("#tenthWrap"),
      document.querySelector("#elevenWrap"),
      document.querySelector("#readybar"),
    ]).on('drop', function(el){
      let ready = [];
      for(let i = 0; i < 11; ++i){
        if(document.querySelectorAll(".textWrap")[i].childNodes.length == 1){
          ready.push(document.querySelectorAll(".textWrap")[i].children[0].innerText);
        }
      }
      if(JSON.stringify(['', '', '', '', ' ', '', '', ' ', '', '', ' ']) === JSON.stringify(ready)){
        document.querySelector("#next").click();
      }
    });
  	document.querySelector(".coin").style.bottom = "calc(87% - 25px)";
  	document.querySelector(".bar").style.height = "87%";
  }
}
</script>

<style scoped>
.textWrap{
  display: inline;
}
span{
  display: inline;
  margin: 10px;
}
#Start, .task{
  display: block;
}
.text{
  position: absolute;
  bottom: 0;
  max-width: calc(100vw - 50px);
  right: 0;
}
#readybar{
  width: 100%;
  max-width: 100%;
  max-height: 100%;
  height: auto;
}
.barcontainer{
  top: 50vh !important;
}
#wrapHelp{
  transform: none;
  top: -97vh;
  left: 70px;
  width: calc(95vw - 70px);
  height: 65vh;
}
#taskbar{
  width: 100% !important;
  height: 100% !important;
  max-width: 100% !important;
  max-height: 100% !important;
  overflow-y: auto;
  text-align: justify;
}
</style>


